import { BrowserRouter, Route, Routes } from "react-router-dom";
import Login from "./page/Welcome";
import Path from "./page/Path";
import Map from "./page/Map";
import Welcome from "./page/Welcome";


import Register from "./page/Register";
import Background from "./components/Background";


function App() {
  return (
    <div>
      <BrowserRouter>
      <Routes>
      <Route
          path="/"
          element={
            <Background>
              <Welcome />
            </Background>
          }
        />
        <Route
          path="/login"
          element={
            <Background>
              <Login />
            </Background>
          }
        />
        <Route
          path="/path"
          element={
            <Background>
              <Path />
            </Background>
          }
        />
        <Route
          path="/map"
          element={
            <Background>
              <Map />
            </Background>
          }
        />
        <Route
          path="/regist"
          element={
            <Background>
              <Register />
            </Background>
          }
        />
      </Routes>
    </BrowserRouter>
      
    </div>
  );
}

export default App;
